







import { Prop, Component, Vue } from "vue-property-decorator";

declare module "vue/types/vue" {
  interface Vue {
    pokemonName: string;
  }
}

@Component({
  data() {
    return {
      pokemonName: ""
    };
  },
  methods: {
    getPokemonName() {
      this.$parent.fetchData(this.pokemonName);
    }
  }
})
export default class PokeSearch extends Vue {}
